import httpAxios from '../axios';
import qs from 'qs';

export default {
    // 获取角色列表
    getRoleList(params: object) {
        return httpAxios.get('/api/a/u/role', { params });
    },
    // 获取角色权限
    getModuleList() {
        return httpAxios.get('/api/a/u/moduleList');
    },
    // 根据角色id查询权限
    getRoleModule(params: object) {
        return httpAxios.get('/api/a/u/roleModule', { params });
    },
    // 获取全部模块
    getParenAllModule() {
        return httpAxios.get('/api/a/u/parentAllModule');
    },
    // 编辑角色名称及权限
    putRole(params: object) {
        return httpAxios.put('/api/a/u/role', params);
    },
    // 新增角色
    postRole(params: object) {
        return httpAxios.post('/api/a/u/role', params);
    },
    // 删除角色
    deleteRole(id: number) {
        return httpAxios.delete(`/api/a/u/role/${id}`);
    },
};
